import classNames from 'classnames';
import { useId } from 'hooks';
import './index.css';

function Monogram({ highlight, className, ...props }) {
  const id = useId();
  const clipId = `monogram-clip-${id}`;

  return (
    <svg
      aria-hidden
      className={classNames('monogram', className)}
      width="46"
      height="50"
      viewBox="0 0 46 50"
      {...props}
    >
      <defs>
        <clipPath id={clipId}>
          <path d="M 23 33 l 9.475 -18.462 l -8.475 -10.538 L 40 9 L 25 13 l -6 15 l -6 -14 L 7 0 A 6.698 6.698 0 0 0 5.942 0 h -4.6 a 0.842 0.842 0 0 0 -0.748 1.23 L 18 40 a 0.839 0.839 0 0 0 1.524 -0.092 z a 0.835 0.835 0 0 0 -0.525 -0.462 z" />
        </clipPath>
      </defs>
      <rect clipPath={`url(#${clipId})`} width="100%" height="100%" />
      {highlight && (
        <g clipPath={`url(#${clipId})`}>
          <rect className="monogram__highlight" width="100%" height="100%" />
        </g>
      )}
    </svg>
  );
}

export default Monogram;
